h1, h2, h3, h4, h5, p, a, input, span {
    font-family: Poppins, sans-serif;
    font-size: 20px;
}
.fish-header {
    background-color: #0039BD82;
    position: fixed;
    width: 100%;
    height: 86px;
    z-index: 9;
    @include bp(laptop) {
        height: 84px;
    }
    .header-wrapper-mob {
        display: none;
        @include bp(tablet-all) {
            display: block;
            height: 100%;
        }
        .mob-wrap {
            display: flex;
            justify-content: space-between;
            width: 100%;
            .logo {
                display: flex;
                align-items: center;
                img {
                    height: 60px;
                    width: auto;
                }
            }
            .fish-navigation-mob {
                background-color: #fff;
                // padding-top: 65px;
                position: absolute;
                right: 0;
                left: 0;
                top: 85px;
                display: none;
                justify-content: center;
                align-items: center;
                transition: ease-in-out 0.3s;
                transition:0.4s;
                -webkit-box-shadow: 0px 6px 13px -3px rgba(120,120,120,1);
                -moz-box-shadow: 0px 6px 13px -3px rgba(120,120,120,1);
                box-shadow: 0px 6px 13px -3px rgba(120,120,120,1);
                .mob-menu {
                    transition:0.4s;
                    height:100%;
                    width:100%;
                    background-color: rgba(255,255,255, 0.8);
                    z-index:9999;
                    text-align:center;
                    padding: 30px 0;
                    .menu-item {
                        padding: 10px;
                        border-bottom: 1px solid #efefef;
                        a {
                            padding: 10px 0;
                            font-size: 18px;
                            font-weight: 500;
                            color: #2b59c3;
                            &:hover {
                                color: #95daf3;
                                transition: ease-in-out 0.3s;
                            }
                        }
                    }
                    .menu-item-has-children {
                        position: relative;
                        .made-chevron {
                            position: absolute;
                            right: 15px;
                            top: 8px;
                            i {
                                &::before {
                                    color: #2b59c3;
                                }
                            }
                        }
                        .dropdown-some {
                            margin-top: 12px;
                            display: none;
                            transition: ease-in-out 0.1s;
                            background-color: rgba(0,57,189,.5098039216);
                        }
                    }
                }
            }
            .burger-menu {
                display: flex;
                .bar {
                    transition:0.4s;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    padding: 25px;
                    .bar-1 {
                        width:5em;
                        display: block;
                        width: 40px;
                        border-top: 6px solid white;
                        padding-top:6px;
                        cursor:pointer;
                        transition:0.3s;
                      }
                       .bar-2 {     
                        width:5em;
                        display: block;
                        width: 40px;
                        border-top: 6px solid white;
                        padding-top:5px;  
                        display:fixed;
                        transition:0.3s;
                        }
                         .bar-3 {  
                      
                        width:5em;
                        display: block;
                        width: 40px;
                        border-top: 6px solid white;
                        padding-top:5px;
                        transition:0.3s;
                        }
                 }
                 .change {
                    .bar-1 {
                        -webkit-transform: rotate(-45deg) translate(-10px, 5px);
                            transform: rotate(-45deg) translate(-10px, 8px);
                    }
                    .bar-2 {opacity:0;}
                    .bar-3 {
                        -webkit-transform: rotate(45deg) translate(-10px, 5px);
                        transform: rotate(45deg) translate(-10px, -4px);
                    }
                 }
            }
        }
    }
    .header-wrapper {
        @include bp(tablet-all) {
            display: none;
        }
        .desk-wrap {
            display: flex;
            width: 100%;
            padding-bottom: 0px;
            padding-top: 5px;
            .logo {
                img {
                    height: 70px;
                    width: auto;
                }
            }
            .fish-navigation {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: end;
                .desk-menu {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    .menu-item {
                        padding: 21px 23px;
                        @include bp(laptop) {
                            padding: 25px 23px;
                        }
                        a {
                            font-size: 26px;
                            font-weight: 600;
                            line-height: 1px;
                            word-spacing: 6px;
                            color: #fff;
                            padding: 18px 0px;
                            margin: 0 10px;
                            height: fit-content;
                            display: block;
                            @include bp(laptop) {
                                font-size: 22px;
                                padding: 13px 0;
                            }
                        }
                    }
                    .menu-item-has-children {
                        position: relative;
                        // .made-chevron {
                        //     position: absolute;
                        //     right: -18px;
                        //     top: -5px;
                        //     i {
                        //         &::before {
                        //             color: #fff;
                        //         }
                        //     }
                        // }
                        &:hover .dropdown-some {
                            height: auto;
                            visibility: visible;
                            transition: ease-in-out 0.2s;
                        }
                        .dropdown-some {
                            padding: 10px 5px;
                            position: absolute;
                            height: 0;
                            visibility: hidden;
                            transition: ease-in-out 0.2s;
                            top: 80px;
                            left: -10px;
                            background-color: rgba(0,57,189,.5098039216);
                            width: 235px;
                            @include bp(laptop) {
                                top: 78px;
                            }
                            .menu-item {
                                margin: 0;
                                padding: 10px 5px;
                                a {
                                    font-size: 20px;
                                    padding: 5px 0;
                                    &:hover {
                                        color: #95daf3;
                                        transition: color ease-in-out 0.2s;
                                    }
                                }
                            }
                        }
                    }
                    .current_page_item a {
                        border-bottom: 1px solid #fff;
                    }
                }
            }
        }
    }
}




